/* Media Queries */

/* Standard desktop  */
@media (max-width: 1920px) {
  :root {
    --content-padding: 4rem;
  }
}

/* Old desktop or large scaled UI */
@media (max-width: 1280px) {
  :root {
    font-size: 8px;
  }
}

/* Tablet landscape */
@media (max-width: 1024px) {
  :root {
    font-size: 10px;
    --content-width: 100%;
    --content-padding: 2rem;
  }
}

/* Tablet portrait */
@media (max-width: 800px) {
  :root {
    font-size: 9px;
  }
}

/* Smartphones */
@media (max-width: 550px) {
  :root {
    font-size: 7px;
    --content-padding: 1rem;
  }
}
