/* Globals */
body {
  font-family: "Space Mono", monospace;
  font-size: 2.4rem;
  font-weight: 400;
  line-height: 1.4;
  background-color: var(--light-grayish-cyan);
}

section {
  padding: var(--content-padding);
}

p,
ul,
figure {
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.1;
}

h1 {
  font-size: 4rem;
  margin-bottom: 3rem;
}
h2 {
  font-size: 3rem;
  margin-bottom: 2rem;
}
h3 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}
h4 {
  font-size: 2.2rem;
  font-weight: 700;
}

button {
  font-family: "Space Mono", monospace;
}
